import styled, { keyframes } from "styled-components";

export const ErrorGetDataBoxStyle = styled.section`
  min-height: ${(props) => props.name === "team" && "150px"};

  background-color: ${(props) =>
    props.name === "team" ? `${props.theme.colors.red.medium}` : "#fff"};

  box-shadow: ${(props) =>
    props.name === "team" &&
    `0 0 2px 2px #fff, 0 0 4px 2px ${props.theme.colors.red.medium}`};

  margin-top: ${(props) => props.name === "order" && "40px"};

  display: flex;
  align-items: center;
  justify-content: center;
`;

// LOADING INDICATOR

const animateLoading = keyframes`
0%, 20%, 80%, 100% {
  transform: scale(1);
}
50% {
  transform: scale(1.5);
}
`;

export const LoadingBoxStyle = styled.div`
  margin: 0 auto;
  display: block;
  position: relative;
  width: 80px;
  height: 80px;
`;

export const LoadingItemStyle = styled.div`
  background-color: ${({ theme }) => `${theme.colors.gray.basic}`};

  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  animation: ${animateLoading} 1.2s linear infinite;

  :nth-child(1) {
    animation-delay: 0s;
    top: 37px;
    left: 66px;
  }

  :nth-child(2) {
    animation-delay: -0.1s;
    top: 22px;
    left: 62px;
  }

  :nth-child(3) {
    animation-delay: -0.2s;
    top: 11px;
    left: 52px;
  }

  :nth-child(4) {
    animation-delay: -0.3s;
    top: 7px;
    left: 37px;
  }

  :nth-child(5) {
    animation-delay: -0.4s;
    top: 11px;
    left: 22px;
  }

  :nth-child(6) {
    animation-delay: -0.5s;
    top: 22px;
    left: 11px;
  }

  :nth-child(7) {
    animation-delay: -0.6s;
    top: 37px;
    left: 7px;
  }

  :nth-child(8) {
    animation-delay: -0.7s;
    top: 52px;
    left: 11px;
  }

  :nth-child(9) {
    animation-delay: -0.8s;
    top: 62px;
    left: 22px;
  }

  :nth-child(10) {
    animation-delay: -0.9s;
    top: 66px;
    left: 37px;
  }

  :nth-child(11) {
    animation-delay: -1s;
    top: 62px;
    left: 52px;
  }

  :nth-child(12) {
    animation-delay: -1.1s;
    top: 52px;
    left: 62px;
  }

  @media (min-width: 1024px) {
    width: 8px;
    height: 8px;
  }
`;
